.block {
  padding-bottom: 25px; }
  .block__content {
    border: 1px solid #cc3836;
    background-color: #cc3836;
    border-radius: 10px;
    /* width: 350px; */
    padding: 10px;
    color: #eaeaea;
    box-shadow: 2px 3px 6px 1px #8a8a8a; }
  .block__name {
    margin-bottom: 10px; }
  .block__link {
    color: #eaeaea;
    text-decoration: none; }

.flex {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  height: 100%; }

.footer {
  background-color: #cc3836;
  height: 100px;
  display: flex;
  align-items: center;
  font-family: 'Lato', sans-serif;
  font-size: 20px;
  color: white; }

.header {
  font-family: 'Bowlby One SC', cursive;
  font-size: 50px;
  color: white;
  background-color: #cc3836; }
  .header__content {
    display: flex;
    flex-direction: row; }
  .header__logo {
    margin-right: 10px; }

.logo__img {
  height: 70px;
  width: auto; }

.main {
  background-color: white;
  font-family: 'Lato', sans-serif;
  font-size: 25px;
  flex-grow: 1; }
  .main__first-text, .main__error-text {
    display: none; }

.search {
  background-color: #f2f2f2;
  font-size: 25px;
  position: relative; }
  .search__field {
    background-color: transparent;
    border: none;
    padding-left: 45px;
    width: 100%;
    font-family: 'Lato', sans-serif; }
  .search::before {
    content: "";
    background: url("../img/before.png");
    background-position: center;
    background-size: cover;
    width: 30px;
    height: 30px;
    top: -1px;
    position: absolute; }

.wrapper {
  max-width: 1024px;
  width: 100%;
  margin: 0  auto;
  padding: 20px; }

@media (max-width: 650px) {
  .header__content {
    display: block;
    margin-bottom: 15px; }
  .header__text {
    font-size: 30px; } }

@media (max-width: 400px) {
  .search__field {
    font-size: 20px;
    padding-left: 35px; }
  .search::before {
    width: 25px;
    height: 25px;
    top: 1px; }
  .main {
    font-size: 20px; } }
